import { useEffect } from "react"
import { useDispatch, useSelector } from "react-redux"
import { fetchPhotos } from "../Redux/Action";
import { Container, Row, Col, Card} from "react-bootstrap";
import { Link } from "react-router-dom";
import "./Photos.css"
const Photos = () => {
    const dispatch = useDispatch();
    const photos = useSelector(state => state.photos)
    //const { photos }=reducerState;
    useEffect(() => {
        /*
       let  fetchPhotos= async()=>{
            let promise= await fetch("https://picsum.photos/v2/list")
            let data=await promise.json();
            console.log(data)
            
            
        }
        fetchPhotos();
        */
        dispatch(fetchPhotos())
    }, [dispatch])
    return (
        <div>
            
            <Container>
                <Row>
                    {photos.map((e) =>
                        <Col md={4} key={e.id} className="single-photo" style={{textAlign:"center"}}>
                            <Card>
                                <Card.Img className="photo-image" variant="top" src={e.download_url} />
                                <Card.Body>
                                    
                                    
                                    <Link className="btn btn-primary" to={`/photos/${e.id}`}>Click Here</Link>
                                </Card.Body>
                            </Card>
                        </Col>


                    )}
                </Row>

            </Container>
        </div>
    )
}
export default Photos;