import { FETCH_PHOTOS_REQUESTED, FETCH_PHOTOS_SUCCESS } from "./Action";

let initialState = {
    loading: false,
    photos: []
}

export const Reducer = (state = initialState, action) => {
    switch (action.type) {
        case FETCH_PHOTOS_REQUESTED:
            return {
                ...state,
                loading: true
            }

        case FETCH_PHOTOS_SUCCESS:
            return {
                ...state,
                loading: false,
                photos:action.payload
            }

        default: 
            return state    
    }
}