import axios from 'axios';

export const FETCH_PHOTOS_REQUESTED = 'FETCH_PHOTOS_REQUESTED'
export const FETCH_PHOTOS_SUCCESS ='FETCH_PHOTOS_SUCCESS'

export const fetchPhotosRequested=()=>({
    type: FETCH_PHOTOS_REQUESTED
})

export const fetchPhotosSuccess=(photos)=>({
    type: FETCH_PHOTOS_SUCCESS,
    payload: photos
})

export const fetchPhotos= ()=>{
    
    return async (dispatch,getState) =>{
        try{
            dispatch(fetchPhotosRequested())
            let res = await axios.get('https://picsum.photos/v2/list')
            //let data =await res.text()
            console.log(res.data)
            dispatch(fetchPhotosSuccess(res.data))
        }catch(error){ console.log(error.message) }
    }


}